import { AppTypescript } from '..';
export class Implementation {
    app: AppTypescript;
    constructor(app: AppTypescript) {
        this.app = app;
        this.Init();
    }
    Init() {
        this.app.divText.style.margin = '10px';
        this.app.divText.innerHTML = '<p>version of Typescript: 3.6.4</p>';
    }
}